@import "variables";

body{
  .header__fixedNav {
    display: none;
    width: 86%;
    position: fixed;
    top: 0;
    left: 50%;
    margin-left: -43%;
    z-index: 15;
    height: 50px;
    background-color: @mainColor;
    ul {
      display: flex;
      background-color: @fontColor;
      li {
        a{
          display: flex;
          justify-content: center;
          align-items: center;
          text-decoration: none;
          font: 1.125em/50px @sansregular;
          color: #fff;
          i{
            font-size: 1.5em;
            line-height: 50px;
          }
        }
        &:nth-child(1) {
          width: 20%;
          background-color: #77c9cc;
          a {
            font-family: @sansbold;
            color: @linkColor;
            i {
              padding: 0 15px 0 20px;
            }
            &:hover {
              color: #fff;
              background-color: @hoverColor;
              transition: .5s;
            }
          }
          .mobile{
            display: none;
          }
        }
        &:nth-child(2) {
          width: 55%;
          display: flex;
          justify-content: center;
          align-items: center;
          .desktop{
            width: 100%;
            height: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
            input {
              width: 90%;
              height: 35px;
              padding-left: 25px;
              font: 1em/50px @sansregular;
              color: #9e9e9e;
              border: 3px solid @fontColor;
              border-radius: 50px;
              outline: none;
            }
            i {
              font-size: 1.2em;
              line-height: 45px;
              position: relative;
              left: -50px;
              color: @mainColor;
            }
          }
          .mobile{
            width: 100%;
            display: none;
            justify-content: center;
            align-items: center;
            position: relative;
            input{
              width: 345%;
              height: 35px;
              padding-left: 25px;
              position: absolute;
              left: 0;
              font: 1em/50px @sansregular;
              color: #9e9e9e;
              border: 3px solid @fontColor;
              border-radius: 50px;
              outline: none;
              z-index: 5;
            }
            i{
              font-size: 2.1em;
              color: #fff;
              line-height: 50px;
              &:hover{
                cursor: pointer;
              }
            }
            .back{
              font-size: 1.2em;
              line-height: 45px;
              position: relative;
              left: -35px;
              color: @mainColor;
              z-index: 5;
            }
          }
        }
        &:nth-child(3) {
          width: 17%;
          .mobile{
            display: none;
          }
        }
        &:nth-child(4) {
          width: 3%;
          a {
            i {
              font-size: 2.5em;
              color: #7d3daf;
              background-color: #fff;
            }
          }
        }
        &:nth-child(5) {
          width: 15%;
          .mobile{
            display: none;
          }
        }
      }
    }
  }
}