@import "components/variables";
@import "components/mixin";
@import "components/header";
@import "components/accordeon";
@import "components/howToMakeOrder";
@import "components/addres";
@import "components/deliveryPopup";
@import "components/fixed_nav";
@import "components/authorization";
@import "components/aside_buttons";

body{
  .header{
    position: unset!important;
  }
  .content{
    padding-top: 25px;
    width: 100%;
    .containerParent();
    .container{
      .containerChild();
      display: grid;
      grid-template-areas: "aside breadcrumb"
                           "aside catalog"
                           "order order";
      grid-template-columns: 25% 1fr;
      .content__aside{
        grid-area: aside;
      }
      .content__catalog__breadcrumb{
        grid-area: breadcrumb;
        ul {
          display: flex;
          li {
            a {
              font: .875em/1em @sansregular;
              color: #474747;
            }
            span {
              font: .875em/1em @sansregular;
              color: #474747;
            }
          }
        }
      }
      .content__catalog{
        grid-area: catalog;
        h5 {
          grid-area: headline;
          font: 2.25em/2.375em @serifitalic;
          color: @linkColor;
        }
        .catalog__options{
          display: flex;
          justify-content: space-between;
          .catalog__options__goods,
          .catalog__options__sortBy{
            display: flex;
            align-items: center;
            position: relative;
            p{
              font: .875em/1em @sansregular;
              span{
                font: 1em/1em @sansbold;
              }
            }
            select{
              margin: 0 0 0 10px;
              padding: 0 0 0 15px;
              width: 200px;
              height: 30px;
              font: .875em/30px @sansbold;
              color: @mainColor;
              background:#fff url("../img/down-arrow.png") no-repeat 96% center;
              border: 1px solid #e6e6e6;
              border-radius: 5px;
              outline: none;
              appearance: none!important;
              option{
                border: 1px solid #e6e6e6;
                border-radius: 5px;
                outline: none;
              }
              &:hover{
                cursor: pointer;
              }
            }
          }
        }
        .catalog__goods{
          display: flex;
          flex-wrap: wrap;
          div {
            margin: 15px;
            width: 225px;
            position: relative;
            text-align: center;
            border: 1px solid #e6e6e6;
            border-radius: 5px;
            box-sizing: border-box;
            a {
              display: block;
              position: relative;
              text-decoration: none;
              img {
                margin-top: 15px;
                width: 200px;
                height: 200px;
              }
              p {
                padding: 10px;
                font: 1em/1.125em @sansregular;
                color: @linkColor;
                text-align: left;
              }
              .price {
                font: 1.25em/1.375em @sansbold;
                span {
                  font: .875em/1em @sansregular
                }
              }
              .favorite{
                position: absolute;
                right: 10px;
                bottom: 0;
                i{
                  font-size: 1.375em;
                  color: @mainColor;
                }
              }
            }
            .hover{
              padding: 0;
              margin: 0;
              width: 100%;
              height: 100%;
              display: none;
              position: absolute;
              top: 0;
              z-index: 2;
              border: 1px solid #77c9cc;
              border-radius: 5px;
              box-shadow: 5px 5px 10px #e6e6e6;
              .link{
                display: block;
                position: absolute;
                width: 100%;
                height: 100%;
                background-color: rgba(119, 201, 204, .3);
              }
              .addBusket,
              .addFavorite{
                display: block;
                width: 75%;
                height: 40px;
                position: relative;
                z-index: 1;
                font: .875em/40px @sansregular;
                text-transform: uppercase;
              }
              .addBusket{
                margin: 0 0 40px;
                color: #fff;
                background-color: @mainColor;
                border-radius: 5px;
              }
              .addFavorite{
                margin: 0 0 10px;
                color: @linkColor;
                background-color: #fff;
                border: 1px solid @linkColor;
                box-sizing: border-box;
                border-radius: 5px;
              }
            }
            &:nth-child(3n+1){
              margin: 15px 15px 15px 0;
            }
            &:hover {
              border-color: @fontColor;
              cursor: pointer;
              transition: .5s;
              .hover{
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                align-items: flex-end;
                cursor: default;
              }
            }
          }
        }
        .content__showMore{
          width: 81%;
          margin: 15px 0 50px;
          display: flex;
          justify-content: center;
          a {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 225px;
            height: 40px;
            margin: 15px 50px;
            font: 1em/40px @sansregular;
            color: #176975;
            text-decoration: none;
            background-color: transparent;
            border: 1px solid #176975;
            border-radius: 5px;
            i{
              font-size: 1.25em;
              line-height: 40px;
            }
            &:hover {
              color: #fff;
              background-color: #176975;
              cursor: pointer;
              transition: .5s;
            }
          }
        }
        .content__paginator{
          width: 81%;
          display: flex;
          justify-content: center;
          ul{
            display: flex;
            li{
              a{
                margin: 0 5px;
                display: block;
                width: 40px;
                height: 40px;
                font: 1em/40px @sansregular;
                color: @mainColor;
                text-align: center;
                text-decoration: none;
                border: 1px solid @mainColor;
                border-radius: 5px;
                &:hover{
                  background-color: #f6f6f6;
                }
              }
              .active{
                background-color: #f6f6f6;
              }
            }
          }
        }
        .content__aboutCategory{
          margin: 70px 0 0;
          width: 65%;
          h5 {
            grid-area: headline;
            font: 2.25em/2.375em @serifitalic;
            color: @linkColor;
          }
          p{
            margin: 0 0 20px;
            font-size: 1em/1.125em @sansregular;
          }
        }
      }
      .content__howToMakeOrder{
        grid-area: order;
      }
    }
  }
  ::-webkit-scrollbar{
    width: 10px;
  }
  ::-webkit-scrollbar-button{
    width: 10px;
    height: 0;
  }
  ::-webkit-scrollbar-thumb{
    width: 5px;
    border: 1px solid @linkColor;
    border-radius: 5px;
    background-color: @fontColor;
    box-shadow: 0 1px 1px #fff inset;
  }
  ::-webkit-scrollbar-track{
    width: 5px;
    background-color: @mainColor;
  }
  ::-webkit-scrollbar-track-piece{
    box-shadow: 0 1px 1px #000 inset;
  }
}